<template>
    <div class="helix-display">
        <div class="card-title-float card-title">Realspace
            <span v-show="!planarMode">Helix</span>
            <span v-show="planarMode">Lattice</span>
        </div>
        <div class="top-realspace-buttons" v-if="fullscreen">
            <div v-if="planarMode" class="planarMode-list">
                <v-tooltip bottom >
                    <template v-slot:activator="{ on }">
                        <v-btn dark color="var(--primary)" v-on="on" fab
                               @click="planarOptions.basis = !planarOptions.basis"
                               class="top-realspace-item">
                            <v-icon>mdi-axis-arrow-info</v-icon>
                        </v-btn>
                    </template>
                    <span v-show="planarOptions.basis">Hide lattice basis vectors</span>
                    <span v-show="!planarOptions.basis">Show lattice basis vectors</span>
                </v-tooltip>
                <v-tooltip bottom>
                    <template v-slot:activator="{ on }">
                        <v-btn dark color="var(--primary)" v-on="on" fab
                               @click="planarOptions.axis = !planarOptions.axis"
                               class="top-realspace-item">
                            <v-icon>mdi-axis-arrow</v-icon>
                        </v-btn>
                    </template>
                    <span v-show="planarOptions.axis">Hide axis system</span>
                    <span v-show="!planarOptions.axis">Show axis system</span>
                </v-tooltip>
                <div class="h-spacer top-realspace-item"></div>
                <v-tooltip bottom>
                <template v-slot:activator="{ on }">
                    <v-btn dark color="var(--primary)" v-on="on" fab
                           @click="planarMode = false"
                           class="top-realspace-item">
                        <v-icon>mdi-dna</v-icon>
                    </v-btn>
                </template>
                <span>Roll up lattice into helix</span>
            </v-tooltip>
            </div>
            <v-tooltip bottom  v-if="!planarMode">
                <template v-slot:activator="{ on }">
                    <v-btn dark color="var(--primary)" v-on="on" fab
                            @click="planarMode = true"
                            class="top-realspace-item">
                        <v-icon>mdi-dots-hexagon</v-icon>
                    </v-btn>
                </template>
                <span>Unroll into lattice</span>
            </v-tooltip>
            <div class="lattice-basis" v-if="planarMode && planarOptions.basis">
                <v-card class="mx-auto" max-width="344" outlined>
                    <v-list-item three-line>
                        <v-list-item-content>
                            <div class="overline mb-4">
                                Lattice parameters
                            </div>
                            <v-list-item-subtitle>Select helix from dropdown list.</v-list-item-subtitle>
                        </v-list-item-content>
                    </v-list-item>
                    <v-card-actions>
                        <v-btn outlined rounded text disabled>
                            Show in realspace image
                        </v-btn>
                    </v-card-actions>
                </v-card>
            </div>
        </div>
        <div v-show="helixFamily.length > 0" class="realspace-controls">
            <div>
            <v-tooltip right>
                <template v-slot:activator="{ on }">
                    <v-btn dark

                            fab
                            color="var(--primary)"
                            @click="rotate = !rotate"
                            v-on="on"
                            class="left-realspace-item"
                            :hidden="planarMode"
                    >
                        <v-icon v-if="rotate">mdi-pause</v-icon>
                        <v-icon v-if="!rotate">mdi-play</v-icon>
                    </v-btn>
                </template>
                <span>Toggle auto-rotation</span>
            </v-tooltip>
            </div>
            <div class="button-fullscreen">
            <v-tooltip top>
                <template v-slot:activator="{ on }">
                    <v-btn
                            dark
                            fab
                            color="var(--primary)"
                            @click="toggleFullscreen"
                            v-on="on"
                    >
                        <v-icon v-if="fullscreen">mdi-fullscreen-exit</v-icon>
                        <v-icon v-if="!fullscreen">mdi-fullscreen</v-icon>
                    </v-btn>
                </template>
                <span>Fullscreen</span>
            </v-tooltip>
            </div>
        </div>
    </div>
</template>

<script>
    import * as THREE from 'three';
    import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls'
    import { OutlineEffect } from 'three/examples/jsm/effects/OutlineEffect.js';

    export default {
        name: "HelixDisplay",
        props: {
            helixFamily: {
                type: Array,
            },
        },
        watch: {
            // eslint-disable-next-line no-unused-vars
            helixFamily: {
                handler: function () {
                    this.refreshHelix()
                },
                deep: true
            },
            planarMode: {
                handler: function () {
                    this.refreshHelix()
                    this.controls.target.set( 0, 0, this.cam_z );  // also reset the pan
                    this.camera.position.set( 0, -this.cam_radius, this.cam_z ); // also reset camera
                },
            },
            planarOptions: {
                handler: function () {
                    this.refreshHelix()
                },
                deep: true
            },
        },
        data: () => ({
            scene: '',
            rotationRate: 0.01,
            rotate: true,
            fullscreen: false,
            camera: '',
            cam_radius: 160,
            cam_z: 50,
            renderer: '',
            controls: null,
            raycaster: null,
            mouse:null,
            scaleFac: 1,
            crosshair: {horizontal:null, vertical:null},
            effect: '',
            container: '',
            colors: [
                new THREE.Color(211/255, 94/255, 96/255),  // red
                new THREE.Color(51/200, 62/200, 91/200),   // indigo
                new THREE.Color(128/255, 133/255, 133/255),  // gray
                new THREE.Color(144/255, 103/255, 167/255),
                new THREE.Color(114/255, 147/255, 203/255),
                new THREE.Color(225/255, 151/255, 76/255),
                new THREE.Color(132/255, 186/255, 91/255),
                new THREE.Color(204/255, 194/255, 16/255),
            ],
            planarMode: false, // display helix as 2D lattice? (default = no = false)
            planarOptions: {axis: true, basis: false}
        }),
        methods:{
            refreshHelix() {
                console.log('redrawing the helix (three.js)');
                this.planarMode ? this.drawLattice() : this.drawHelix()
            },

            drawHelix() {
                console.log('drawing a realspace helix...');
                // clear the old helices
                while(this.scene.children.length > 0){
                    this.scene.remove(this.scene.children[0]);
                }

                // compute relevant scale to draw helix at (we also consider the unit size)
                let max_radius= Number(this.helixFamily[0]['radius']) + Number(this.helixFamily[0]['unit_size']) ;
                for (let helix of this.helixFamily) {
                    let draw_radius = Number(helix['radius']) + Number(helix['unit_size']);
                    if ( 1/ draw_radius < 1/max_radius ){
                        max_radius = draw_radius
                    }
                }
                const scalefac = 10/max_radius;  // the prefactor is an empirical value and might need to be made more robust

                // define handedness
                let handedness = 1;  // right handed=1, left handed =-1

                for ( let [ind, helix] of this.helixFamily.entries()) {
                     if (ind < this.colors.length) {
                        this.material = new THREE.MeshToonMaterial( { color: this.colors[ind] });
                    }

                    // set handedness for this helix
                    if (helix['handedness'] === 'left') {
                        handedness = -1;
                    } else {
                        handedness = 1;
                    }

                    let amount = Math.ceil( 100 / ( helix['rise']*scalefac ) );  // number of helical units to draw
                    if (amount > 1000) { amount = 1000}
                    let geometry = new THREE.SphereGeometry( helix['unit_size']*scalefac, 32,16 );

                    // build the helix
                    for ( var i = 0; i < amount ; i ++ ) {
                        let object = new THREE.Mesh( geometry, this.material.clone() );
                        let angle = Number(helix['rotation'])/180*Math.PI +  handedness*i*2*Math.PI / helix['frequency'] ;
                        object.position.x = helix['radius'] * scalefac * Math.cos( angle );
                        object.position.y = helix['radius'] * scalefac * Math.sin( angle);
                        object.position.z = i*helix['rise'] * scalefac + helix['offset']*scalefac;

                        this.scene.add( object );
                    }
                }

                this.controls.enableRotate = true; // make sure you can rotate with the mouse.
                this.addLights();
                this.refreshDisplay( this.container );
            },

            drawLattice() {
                console.log('unfolding helix into lattice and drawing lattice...');
                // clear the old helices
                while(this.scene.children.length > 0){
                    this.scene.remove(this.scene.children[0]);
                }

                // compute relevant scale to draw helix at (we also consider the unit size)
                let max_radius= Number(this.helixFamily[0]['radius']) + Number(this.helixFamily[0]['unit_size']) ;
                for (let helix of this.helixFamily) {
                    let draw_radius = Number(helix['radius']) + Number(helix['unit_size']);
                    if ( 1/ draw_radius < 1/max_radius ){
                        max_radius = draw_radius
                    }
                }
                this.scaleFac = 10/max_radius;  // the prefactor is an empirical value and might need to be made more robust

                const draw_circumference = Number(this.helixFamily[0]['radius']) * this.scaleFac * 2 * Math.PI; // needed for display only

                // define handedness
                let handedness = 1;  // right handed=1, left handed =-1

                for ( let [ind, helix] of this.helixFamily.entries()) {
                    if (ind < this.colors.length) {
                        this.material = new THREE.MeshToonMaterial( { color: this.colors[ind] });
                    }

                    // set handedness for this helix
                    if (helix['handedness'] === 'left') {
                        handedness = -1;
                    } else {
                        handedness = 1;
                    }

                    let amount = Math.ceil( 100 / ( helix['rise']*this.scaleFac ) );  // number of helical units to draw
                    if (amount > 1000) { amount = 1000}
                    let geometry = new THREE.SphereGeometry( helix['unit_size']*this.scaleFac, 32,16 );

                    const circumference = helix['radius'] * this.scaleFac * 2*Math.PI;

                    // build the lattice that would be obtained by unfolding the helix
                    for ( var i = 0; i < amount ; i ++ ) {
                        let object = new THREE.Mesh( geometry, this.material.clone() );
                        let angle = Number(helix['rotation'])/180*Math.PI +  handedness*i*2*Math.PI / helix['frequency'] ;
                        // we now take the X coordinate as if we had unrolled the helix, but we take the remainder
                        // as a function of the helix circumference (and subtract half so it's displayed in center)
                        object.position.x = ((helix['radius'] * this.scaleFac * angle) % circumference) - circumference/2;
                        object.position.z = i*helix['rise'] * this.scaleFac + helix['offset']*this.scaleFac;
                        this.scene.add( object );
                    }
                }

                this.rotate = false; // we do not want rotation in the planar mode (doesn't make sense to have it)
                this.controls.enableRotate = false; // disable panning with the mouse (doesnt make sense in 2D)

                if (this.planarOptions.axis) { this.drawAxes(-draw_circumference/2) }
                if (this.planarOptions.basis) { this.drawBasisVectors( true, -draw_circumference/2) }
                this.scene.add(this.crosshair.vertical);
                this.scene.add(this.crosshair.horizontal);
                this.addLights();
                this.refreshDisplay( this.container );
            },

            addLights(){
                let light = new THREE.AmbientLight( 0xffffff, 0.75  ); // soft white light
                this.scene.add( light );

                const directionalLight = new THREE.DirectionalLight( 0xffffff, 1 );
                directionalLight.position.set( 1, -1, 1 ).normalize();
                directionalLight.intensity = 0.25
                directionalLight.castShadow = true;
                this.scene.add( directionalLight );
            },

            setupDisplay(element_name){
                // get the div we will draw in
                this.container = document.querySelector( element_name );
                this.scene = new THREE.Scene();
                //this.scene.background = new THREE.Color( );


                // setup render window
                this.renderer = new THREE.WebGLRenderer( { antialias: true, alpha: true} );
                this.renderer.setPixelRatio( window.devicePixelRatio );
                this.renderer.setSize( this.container.offsetWidth , this.container.offsetHeight );
                let GLwindow = this.container.appendChild( this.renderer.domElement );
                GLwindow.style.display = 'block'; // really important to prevent ghost whitespace below canvas

                this.effect = new OutlineEffect( this.renderer );  // add black outline to helix subunits
                // set up the camera
                THREE.Object3D.DefaultUp = new THREE.Vector3( 0,0,1);
                this.camera = new THREE.PerspectiveCamera( 40, this.container.offsetWidth/this.container.offsetHeight,
                    0.1, 5000 );
                this.cameraTarget = new THREE.Vector3( 0, 0, this.cam_z );

                //setup raycaster
                this.raycaster = new THREE.Raycaster();
                this.mouse = new THREE.Vector2();

                // set up the axis overlay (only for 2D mode)
                let points = [];
                points.push( new THREE.Vector3(0,0,0));  // our lines only need 2 vertices
                points.push( new THREE.Vector3(0,0,0));
                const material = new THREE.LineBasicMaterial( { color: 0xd3d3d3, depthTest: false } );
                let geometry = new THREE.BufferGeometry().setFromPoints( points );
                this.crosshair.vertical = new THREE.Line( geometry, material );
                this.crosshair.horizontal = new THREE.Line( geometry.clone(), material );

                // further camera setup
                this.controls = new OrbitControls( this.camera, this.renderer.domElement );
                this.controls.autoRotate = this.rotate;
                this.controls.target.set( 0, 0, this.cam_z );
                this.camera.position.set( this.cam_radius, 0, this.cam_z );
                this.controls.enableKeys = false;
                this.controls.update();

                window.addEventListener( 'resize', () => this.refreshDisplay( this.container ), false );  // detect if page resize
                window.addEventListener( 'mousemove', this.onMouseMove, false );  // handle hover


                // function that is repeatedly automatically called
                const animate = () => {
                    requestAnimationFrame( animate );
                    render();
                };

                const render =  () => {
                    this.controls.update();
                    this.controls.autoRotate = this.rotate;
                    this.effect.render(this.scene, this.camera);
                    this.camera.lookAt(this.cameraTarget);

                    if (this.planarMode) {  // only do this for the 2D lattice mode
                        this.raycaster.setFromCamera(this.mouse, this.camera);
                        // calculate objects intersecting the picking ray
                        let intersects = this.raycaster.intersectObjects(this.scene.children);

                        if (intersects.length > 0) {
                            this.drawCrosshair(intersects);
                            this.crosshair.vertical.visible = true;
                            this.crosshair.horizontal.visible = true;
                        } else { // hide crosshair
                            this.crosshair.vertical.visible = false;
                            this.crosshair.horizontal.visible = false;
                        }
                    }
                };

                console.log('[ Realspace Helix display ready. ]');
                animate()
            },

            drawAxes(x_offset) {
                //console.log('Drawing axis system with an x offset of ', x_offset);
                let horizontal= [];
                let vertical= [];
                horizontal.push( new THREE.Vector3(x_offset,0,0));  // our lines only need 2 vertices
                vertical.push(   new THREE.Vector3(x_offset,0,0));
                horizontal.push( new THREE.Vector3(x_offset+500,0,0));
                vertical.push(   new THREE.Vector3(x_offset,0,500));
                let red_material = new THREE.LineBasicMaterial( { color: 0xff8080 } );
                red_material.linewidth = 10;
                const blue_material = new THREE.LineBasicMaterial( { color: 0x0E86D4 } );
                const hori_geometry = new THREE.BufferGeometry().setFromPoints( horizontal );
                const vert_geometry = new THREE.BufferGeometry().setFromPoints( vertical );
                const yaxis = new THREE.Line( hori_geometry, red_material );
                const xaxis = new THREE.Line( vert_geometry, blue_material );
                this.scene.add(xaxis);
                this.scene.add(yaxis);
            },

            drawBasisVectors( zoomPlot, x_offset ) {
                console.log('Drawing basis vectors and zoom setting:', zoomPlot);
                const bases = this.computeBasis();
                let basis;
                for (basis of bases) {
                    const vec_A_end = basis.A;
                    const vec_B_end = basis.B;
                    const origin = new THREE.Vector3(x_offset, 0, 0);
                    const length_A = Math.sqrt(vec_A_end.x ** 2 + vec_A_end.z ** 2);
                    const length_B = Math.sqrt(vec_B_end.x ** 2 + vec_B_end.z ** 2);
                    const hex = 0xEAAA00; // cadetblue, nice colour for both light and dark mode..
                    const vec_A = new THREE.ArrowHelper(vec_A_end, origin, length_A, hex, 0.3*length_A, 2);
                    const vec_B = new THREE.ArrowHelper(vec_B_end, origin, length_B, hex, 0.3*length_B, 2);
                    // ensure it is drawn on top of everything else (except crosshair)
                    vec_A.cone.material.depthTest = false;
                    vec_A.line.material.depthTest = false;
                    vec_B.cone.material.depthTest = false;
                    vec_B.line.material.depthTest = false;
                    this.scene.add(vec_A);
                    this.scene.add(vec_B);
                }

                // if (zoomPlot) {
                //     this.controls.target.set( 0, 0, 0 );
                //     this.cameraTarget = new THREE.Vector3( -x_offset, 0, 0 );  // also reset the pan
                //     this.camera.position.set( -x_offset, -this.cam_radius/4, 0 ); // also reset camera
                // }
            },

            drawCrosshair(intersects) {
                const size = 500;  // width of lines to draw the lines
                //  intersects[0].object.material.color.set(0xff0000); // for testing
                const hover_coords = intersects[0].object.position;
                // update horizontal line ('crosshair.horizontal')
                let cross_x_position = this.crosshair.horizontal.geometry.attributes.position.array;
                cross_x_position[0] = hover_coords.x-size;
                cross_x_position[2] = cross_x_position[5] = hover_coords.z;
                cross_x_position[3] = hover_coords.x+size;
                this.crosshair.horizontal.geometry.attributes.position.needsUpdate = true;
                // update vertical line ('crosshair.vertical')
                let cross_y_position = this.crosshair.vertical.geometry.attributes.position.array;
                cross_y_position[2] = hover_coords.z-size;
                cross_y_position[0] = cross_y_position[3] = hover_coords.x;
                cross_y_position[5] = hover_coords.z+size;
                this.crosshair.vertical.geometry.attributes.position.needsUpdate = true;
            },

            refreshDisplay(container) {  //
                console.log('Refreshing aspect ratio plot');
                this.camera.aspect = container.offsetWidth / container.offsetHeight ;
                this.camera.updateProjectionMatrix();
                this.renderer.setSize( container.offsetWidth, container.offsetHeight  );
            },

            onMouseMove( event ) {
                // calculate mouse position in normalized device coordinates
                // (-1 to +1) for both components
                this.mouse.x = ( event.offsetX / this.renderer.domElement.clientWidth ) * 2 - 1;
                this.mouse.y = - ( event.offsetY / this.renderer.domElement.clientHeight ) * 2 + 1;
            },

            toggleFullscreen(){
                this.fullscreen = !this.fullscreen;

                let card = document.querySelector( '.ui-realspace-panel' );

                if (this.fullscreen) {
                    console.log('[realspace helix] going fullscreen');
                    card.style.position = 'absolute';
                    card.style.width = '90vw';
                    card.style.height = '80vh';  // bit of a bodge
                } else {
                    console.log('[realspace helix] leaving fullscreen');
                    card.style.position = 'static';
                    card.style.width = '0px';
                    card.style.height = 'auto';
                    //this.planarMode = false;  // reset to the helical mode. (not 2D lattice)
                }
                this.refreshDisplay(this.container)
            },

            computeBasis() {
                console.log('computing basis vectors....')
                let bases = [];
                for (let helix of this.helixFamily) {

                    const angle_step = 2*Math.PI / helix['frequency'] ;
                    const x_step = helix['radius'] * this.scaleFac * angle_step;

                    // compute the first (and also trivial) basis vector.
                    const basis_A = new THREE.Vector3( x_step, 0, helix['rise']*this.scaleFac);

                    // compute the second (slightly less trivial) basis vector
                    const circumference = helix['radius'] * this.scaleFac * 2*Math.PI;
                    const x_step_2 =(helix['radius'] * this.scaleFac * angle_step* (Math.ceil(helix['frequency'])) ) % circumference;
                    const basis_B = new THREE.Vector3( x_step_2, 0, helix['rise'] * helix['frequency'] * this.scaleFac );

                    bases.push( {A: basis_A, B: basis_B} );
                    break
                }
                return bases
            }
        },
        mounted() {
            this.setupDisplay( '.helix-display' )
        }
    }
</script>

<style>
    .helix-display{
        height: 100%;
        width: 100%;
        position: relative;
        z-index: 1;
    }

    .realspace-controls{
        padding: 1rem;
        position: absolute;
        display: flex;
        justify-content: space-between;
        width: 100%;
        bottom: 0;
        z-index: 2;
        align-items: flex-end;
    }

    .helix-display canvas{
        position:absolute;
        top:0;
        left:0;
        z-index: 1;
        outline: none
    }

    .top-realspace-buttons {
        position: absolute;
        top: 0;
        right: 0;
        padding: 1rem;
        z-index: 2;
    }

    .top-realspace-item {
        margin-left: 1rem;
    }

    .planarMode-list {
        display: flex;
        justify-content: flex-end;
    }

    .h-spacer{
        width: 3px;
        background-color: var(--primary);
    }

    .lattice-basis{
        margin-top: 5rem;
    }

    @media only screen and (max-width: 600px) {
        .button-fullscreen{
            display: none;
        }

        .helix-display canvas{
            pointer-events: none;
        }
    }
</style>
